* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  .collartitle {
    font-size: 20px;
    line-height: 1;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: Gilroy-regular;
    color: #6481a4;
    text-transform: none;
    font-style: normal;
    text-align: center;
    padding-top: 65px;
  }
  .content {
    font-size: 38px;
    line-height: 1.15;
    letter-spacing: 0;
    word-spacing: 0px;
  
    color: #16314c;
    text-transform: none;
    text-decoration: none;
    font-style: normal;
    text-align: center;
    padding-top: 20px;
  }
  .collars {
    width: 100%;
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 32px 0 25px 0;
    padding-left: 6%;
    padding-right: 6%;
  }
  .collars img {
    margin: 0 auto;
    width: 386px;
    height: 257px;
  }
  .collars p {
    font-size: 20px;
    line-height: 1;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: Gilroy-regular;
    color: #6481a4;
    text-transform: none;
    font-style: normal;
    text-align: center;
  }
  .collars a {
    cursor: pointer;
  }
  img {
    cursor: pointer;
  }
  .collarimg {
    padding: 32px 0px 25px;
  }
  .look {
    background-color: #f4f8fc;
    padding-bottom: 100px;
  }
  .looktitle {
    font-size: 38px;
    line-height: 1.15;
    letter-spacing: 0;
    word-spacing: 0px;
  
    color: #0f2135;
    text-transform: none;
    text-decoration: none;
    font-style: normal;
    text-align: center;
    padding-top: 50px;
  }
  .lookcontent {
    font-size: 20px;
    line-height: 1.25;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: Gilroy-regular;
    color: #16314c;
    text-transform: none;
    text-decoration: none;
    font-weight: 400;
    font-style: normal;
    text-align: center;
    padding-top: 20px;
  }
  .customize {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    text-align: center;
  }
  .customize span {
    font-size: 26px;
    line-height: 1;
    letter-spacing: 0;
    word-spacing: 0px;
  
    color: #0f2135;
    text-transform: none;
    text-decoration: none;
    font-style: normal;
    text-align: center;
  }
  .customize img {
    width: 150px;
    height: 148px;
    text-align: center;
  }
  .customize p {
    font-size: 18px;
    line-height: 1.25;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: Gilroy-regular;
    color: #16314c;
    text-transform: none;
    text-decoration: none;
    font-weight: 400;
    font-style: normal;
    text-align: center;
    padding-top: 10px;
  }
  .contents {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 2% 11%;
  }
  .contents div {
    margin: 20px 25px;
  }
  .contents h2 {
    font-size: 30px;
    line-height: 1.15;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: inherit;
    color: #6481a4;
    text-transform: none;
    text-decoration: none;
    font-weight: 400;
    font-style: normal;
    text-align: left;
    padding-top: 10px;
  }
  .contents p {
    font-size: 18px;
    line-height: 1.25;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: inherit;
    color: #16314c;
    text-transform: none;
    text-decoration: none;
    font-weight: 400;
    font-style: normal;
    text-align: left;
    padding: 15px 0px;
  }
  .contents div button {
    color: #fff;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    font-size: 18px;
    font-weight: bolder;
    background-color: #6481a4;
    cursor: pointer;
    outline: 0 !important;
    border: none;
    width: auto;
    height: auto;
    white-space: normal;
    padding: 15px 45px 15px 45px;
    overflow: hidden;
    border-radius: 5px;
  }
  .contents div button:hover {
    background-color: black;
    color: white;
  }
  .accordion-body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
  }
  .accordion-body img {
    padding-left: 40p x;
  }
  .accordion h2 {
    font-size: 26px;
    line-height: 1;
    letter-spacing: 0;
    word-spacing: 0px;
  
    color: #6481a4;
    text-transform: none;
    text-decoration: none;
    font-weight: bolder;
    font-style: normal;
    text-align: left;
  }
  .accordion div {
    font-size: 18px;
    line-height: 1.25;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: inherit;
    color: #16314c;
    text-transform: none;
    text-decoration: none;
    font-weight: 400;
    font-style: normal;
    text-align: left;
    outline: none;
    border: none;
  }
  .accord {
    background-color: #f4f8fc;
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .backgroundimg {
    background-image: url(./image/25096732.webp);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    margin-top: 90px;
    margin-bottom: 50px;
  }
  .guide h2 {
    font-size: 38px;
    line-height: 1.15;
    letter-spacing: 0;
    word-spacing: 0px;
  
    color: #16314c;
    text-transform: none;
    text-decoration: none;
    font-weight: bolder;
    font-style: normal;
    text-align: center;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .guide p {
    font-size: 18px;
    line-height: 1.25;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: inherit;
    color: #333;
    text-transform: none;
    text-decoration: none;
    font-weight: 400;
    font-style: normal;
    text-align: center;
  }
  .guide {
    width: 60%;
    margin: 0 auto;
  }
  button.accordion-button {
    margin-bottom: 10px;
    margin-top: 10px;
  }
  
  .accordion-button:not(.collapsed) {
    color: black;
    background-color: white !important;
    box-shadow: none !important;
  }
 
  .accordion-item:first-of-type .accordion-button {
      border-top-left-radius: calc(0.25rem - 1px);
      border-top-right-radius: calc(0.25rem - 1px);
  }
  button:focus:not(:focus-visible) {
      outline: 0;
  }
  .accordion-button:focus {
      z-index: 0 !important;
      border-color: none !important;
      outline: 0;
      box-shadow: none !important;
  }
  .trainers h3{
    margin-top: 40px;
   
    font-size: 36px;
    line-height: 1;
    letter-spacing: 0;
    word-spacing: 0px;
    font-family: Gilroy-bold;
    color: #16314c;
    text-transform: none;
    text-decoration: none;
     
    font-style: normal;
    text-align: left;
  }
  .trainers h2{
  letter-spacing: 0;
  word-spacing: 0px;
  font-family: inherit;
  color: #333;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  margin-top: 15px!important;
  margin-bottom: 5px!important;
  font-family: Gilroy-Regular,sans-serif!important;
  color: #5b80a0!important;
  font-size: 22px!important;
  }
  .trainers p{
    
      margin: 0 0 10px !important ;
      padding: 0 !important;
      font-family: Gilroy-Regular,sans-serif;
      color: #333;
      font-size: 18px;
      font-weight: 400;
      text-align: left;
      letter-spacing: normal;
    }
   
  .trainers{
    display: grid;
    grid-gap: 30px;
    grid-template-columns: 30% 70%;
    justify-content: space-evenly;
    margin: 40px auto;
    text-align: center;
    padding-left: 6%;
    padding-right: 6%;
  }